$cAccent: #3978EF;
$cBlack: #121212;
$cGray: #333;
$cLightGray: rgba(#000, 0.1);


.wrapper {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 100px;
    gap: 30px;
}

.accordion-list{
  list-style: none;
  margin: 0;
  padding: 0;
  background-color: #fff;
  max-width: 40rem;
  border-radius: 0.4rem;
  overflow: hidden;
  cursor: pointer;
//   box-shadow:
//     0 0 0.8rem 0.1rem rgba(darken($cAccent, 20%), 0.06),
//     0 20px 30px -10px rgba(darken($cAccent, 20%), 0.2);
    
  &__item{
    &+&{
      border-top: 1px solid $cLightGray;
    }
  }
}

.text {
    position: relative;
    z-index: 100;
    display: flex;
    flex-direction: column;
    & p {
        font-weight: 300;
    font-size: 14px;
    line-height: 1.45;
    color: #6C7074;
    margin-bottom: 20px;
    max-width: 70%;
    }
    & h3 {
        // width: 500px;
line-height: 1.1;
    letter-spacing: 0.0075em;
    margin-bottom: 19px;
    font-size: 40px;
    font-weight: 400;
    margin-top: 5px;
    }    
    & .button1, .button2 {
        font-size: 14px;
    border: 1px solid #012DE7;
    border-radius: 10px;
    padding: 10px 20px 10px;
        text-decoration: none;
        width: 25%;
        margin-bottom: 50px;
        text-align: center;
        background: #4800e8;
        color: #fff;
        margin-top: 30px;
        font-weight: 500;
    }
    & .button2{
        border: 2px solid #4800e8;
        background: transparent;
        color:#4800e8;
    }
    & .button3 {
        background: rgba(1, 45, 231, 0.05);
    border-radius: 6px;
    color: #012DE7;
    padding: 20px 43px;
    font-size: 14px;
    transition: 0.25s;
    max-width: 52%;
    border: 0;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    }
}

.accordion-item{
  $self: &;

  &--opened{    
    #{ $self }__icon{
      transform: rotate(180deg);
    }
        
    #{ $self }__inner{
      max-height: 100rem;
      transition-timing-function: cubic-bezier(0.895, 0.03, 0.685, 0.22);
      transition-duration: 0.5s;
      transition-property: max-height;
    }
    
    #{ $self }__content{
      opacity: 1;
      transform: translateY(0);
      transition-delay: 0.2s;
      transition-timing-function: ease-in-out;
      transition-duration: 0.2s;
      transition-property: opacity, transform;
    }
  }

  &__line{
    display: block;
    padding: 0.8rem 1.2rem;
    display: flex;
    gap: 20px;
    justify-content: flex-start;
    align-items: center;
    background-color: #fff;
    z-index: 2;
    position: relative;
  }
  
  &__title{
    font-size: 1.6rem;
    // margin-left: -100px;
    
    font-weight: 700;
    color: $cBlack;
    font-size: calc(18px + 0 * ((100vw - 320px) / 1600));
    line-height: calc(22px + 0 * ((100vw - 320px) / 1600));
    font-weight: 500;
  }
  
  &__icon{
    width: 1.2rem;
    height: 1.2rem;
    transition: transform 0.3s ease-in-out;
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url(data:image/png;base64,);
    opacity: 0.6;
  }
  
  &__inner{
    max-height: 0;
    overflow: hidden;
    text-transform: cubic-bezier(0.95, 0.05, 0.795, 0.035);
    transition-duration: 0.5s;
    transition-property: max-height;
    z-index: 1;
    position: relative;
  }
  
  &__content{
    opacity: 0;
    transform: translateY(-1rem);
    transition-timing-function: linear, ease;
    transition-duration: 0.1s;
    transition-property: opacity, transform;
    transition-delay: 0.5s;
    padding: 0 1.2rem 1.2rem;
  }
  
  &__paragraph{
    margin: 0;
    font-size: 1rem;
    color: $cGray;
    font-weight: 300;
    line-height: 1.3;
  }
}