.form{
    margin-top: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 20px;
   
    & .formWrapper {
        display: flex;
        justify-content: center;
        align-items: center;    
         border: 0px solid;
    height: auto;
    position: relative;
    background-color: #343750;
    color: #ffffff;
    width: 70%;
    padding: 100px;
    font-size: 20px;

    
    } 

  }
  
  .text {
      width: 50%;
      line-height: 1.6;


      & h2{
        margin-bottom: 20px;
        font-size: 30px;
        text-transform: uppercase;
        font-weight: 400;
      }

      & p{
        font-size: 18px;
        width: 300px;
        margin-bottom: 15px;
      }
    }

    .contact {
      width: 50%;
      display: flex;
      flex-direction: column;
      // justify-content: center;
      // align-items: center;
      & input{
        width: 100px;

      }
    }