

.wrapper {
    width: 100%;
    height: 250vh;
    background-image: url("https://static.wixstatic.com/media/035244_1f9f5504879f46cc90c68e323a18ea59~mv2_d_1920_3986_s_2.jpg/v1/fill/w_1225,h_3636,al_c,q_90,usm_0.66_1.00_0.01,enc_auto/035244_1f9f5504879f46cc90c68e323a18ea59~mv2_d_1920_3986_s_2.jpg");
    position: relative;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;   
    &::before{
        background-color: #3a4c8489;
        content: '';
        display: block;
        height: 250vh;
        position: absolute;
        width: 100%;
      }
      z-index: 1;

      
}
.head{
    position: relative;
        text-align: center;
        padding-top: 150px;
        color: #ffffff;
        z-index: 2;
        padding-bottom: 70px;
        font-weight: 400;
        font-size: 40px;
      }


.outer {
    // position: relative;
    height: 200vh;
    min-width: 20vw;
    flex: 1;
  }
  
  
  
  //-- 

  
  .progress {
    $gap: 20px;
    $line-height: 30px;
    $bullet-radius: 5px;
    $line-thick: 30px;
    $strip-color: #333;
    $next-color: #666;
    $current-color: #333;
    
    $prev-color: #333;
    
  
    display: inline-flex;
    height: 100%;
    padding: 5vh 10%;
    justify-content: center;
  
    > div {
      display: flex;
      flex-direction: column;
      color: $prev-color;
  
      &.left {
        padding-right: $gap;
        text-align: right;
        
        & h2, h4 {
            padding-right: 60px;
            padding-bottom: 10px;
        }
        & p {
            padding-right: 60px;
            line-height: 1.4;
            max-width: 400px;
            }
        // Line
        div {
          &:last-of-type:after {
            display: none;
          }
          
          &:after {
            content: "";
            background: fade_out($strip-color, .9); //rgba(0, 0, 0, 0.6);
            border-radius: 2px;
            position: absolute;
            right: -$gap;
            top: $line-height/2;
            height: 101%;
            width: 1px;
            transform: translateX(50%);
          }
        }
      }
  
      &.right {
        padding-left: $gap;
  
        & h2, h4 {
            padding-left: 60px;
            padding-bottom: 10px;
        }
        & p {
            padding-left: 60px;
            line-height: 1.4;
            max-width: 400px;
            }
        div {        
          &.prev {          
              &:after {
                transition: none;
              }
          }
          
          &.current {
            color: $current-color;
            font-weight: bold;
  
            &:before {
              background: $current-color;
              padding: $bullet-radius * 2;
              transition: all 0.2s .15s cubic-bezier(0.175, 0.885, 0.32, 2);
            }
  
            &:after {
              height: 0%;
              transition: height .2s ease-out;
            }
  
            ~ div {
              color: $next-color;
              
              &:before {
                background: $next-color;
                padding: $bullet-radius * 0.5;
              }
  
              &:after {
                height: 0%;
                transition: none;
              }
            }
          }
  
          // Dot
          &:before {
            content: "";
            background: $strip-color;
            padding: $bullet-radius;
            border-radius: 50%;
            position: absolute;
            left: -$gap;
            top: $line-height/2;
            transform: translateX(-50%) translateY(-50%);
            transition: padding 0.2s ease;
          }
  
          // Line
          &:after {
            content: "";
            background: $strip-color; //rgba(0, 0, 0, 0.6);
            border-radius: 2px;
            position: absolute;
            left: -$gap;
            top: $line-height/2;
            height: 101%;
            width: $line-thick;
            transform: translateX(-50%);
            transition: height 0.2s ease;
          }
        }
      }
  
      div {
        flex: 1;
        //outline: 1px solid rgba(0, 0, 0, 0.1);
        position: relative;
        line-height: $line-height;
        cursor: default;
        min-height: 30px;
        
        &:last-of-type {
          flex: 0;
        }
      }
    }
  }
  
